* {
  margin: 0;
  padding: 0;
  color: rgb(15, 15, 15);
}

html,
body {
  box-sizing: border-box;
}

ul {
  list-style: none;
}

body {
  background-image: url("/images/1.jpg");
  background-size: cover;
  background-position: bottom right;
}

.all {
  width: 100%;
  height: 100%;
  background-color: rgba(143, 142, 142, 0.4);
}

@font-face {
  font-family: "blanchcaps_inline";
  src: url("./fonts/blanch_caps_inline-webfont.eot");
  src: url("./fonts/blanch_caps_inline-webfont.eot?#iefix")
      format("embedded-opentype"),
    url("./fonts/blanch_caps_inline-webfont.woff") format("woff"),
    url("./fonts/blanch_caps_inline-webfont.ttf") format("truetype"),
    url("./fonts/blanch_caps_inline-webfont.svg#blanchcaps_inline")
      format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Josefin Sans";
  src: url("./fonts/JosefinSans-Bold.ttf");
}

@font-face {
  font-family: "Permanent Marker";
  src: url("./fonts/PermanentMarker-Regular.ttf");
}

.header {
  margin: 2rem 4rem;
  background-color: rgba(167, 207, 177, 0.7);
}

h1 {
  font-family: "blanchcaps_inline", sans-serif;
  text-align: left;
  padding-left: 5rem;
  padding-top: 2.5rem;
  font-weight: normal;
  font-size: 7rem;
}

.header h2 {
  font-family: "Permanent Marker", cursive;
  font-weight: 100;
  font-size: 2rem;
  text-align: right;
  padding-right: 1rem;
  color: rgb(146, 102, 110);
}

h2 {
  font-family: "Josefin Sans", sans-serif;
  font-size: 3rem;
  text-align: center;
}

h3 {
  font-family: "Josefin Sans", sans-serif;
}

h4 {
  font-family: sans-serif;
}

.all {
  display: grid;
  grid-template-columns: 7.5fr 2.5fr;
}

.items {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  justify-content: center;
  margin: 1rem 4rem;
  grid-gap: 0.5rem;
}

.deed {
  margin: 1rem;
  background-color: rgb(233, 231, 231);
  border-radius: 0.2rem;
  border: 1px solid rgb(218, 218, 218);
  display: grid;
  justify-items: center;
  grid-gap: 0.4rem;
  transition: 1s;
}

.deed:hover {
  transform: scale(1.05);
}

.deed img {
  height: 10rem;
  max-width: 100%;
}

.deed h3 {
  grid-column: 1/-1;
  padding: 2rem 2rem 0 2rem;
  text-align: center;
}

.deed h4 {
  justify-self: end;
  font-size: 0.8rem;
  align-self: center;
  padding: 0.5rem;
  align-self: end;
  background: rgb(12, 63, 16);
  color: white;
}

.deed button {
  padding: 0.5rem;
  grid-column: 1/-1;
  height: 2rem;
  font-size: 0.8rem;
  border: none;
  border-radius: 1rem;
  color: white;
  background-color: rgb(146, 102, 110);
}

.deed button:hover {
  background-color: rgba(146, 102, 110, 0.6);
}

.deed p {
  padding: 1rem;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 0.8rem;
}

.order {
  padding: 1rem;
  margin: 15rem 1rem 1rem 0;
  background-color: rgba(246, 246, 246, 0.6);
  height: 30rem;
  position: sticky;
  top: 1rem;
}

.order h2 {
  font-size: 2.4rem;
  font-weight: 100;
  color: rgb(37, 82, 37);
}

.order li {
  margin: 1rem 0;
  font-family: sans-serif;
}

.order h3 {
  text-align: center;
  margin: 3rem 0;
}
